<template>
  <div class="login">
    <van-icon name="cross" size="24px" @click="link" />
    <van-tabs @click="onClick" color=" #4d3126">
      <van-tab title="">
        <van-cell-group>
          <van-field
            style="position: relative;
                left: -30px;"
            v-model="username"
            clearable
            label="TW +886"
            placeholder=""
            @click-right-icon="$toast('question')"
          />
          <van-field
            v-model="password"
            type="password"
            right-icon="eye"
            placeholder="(6-20)"
          />
        </van-cell-group>
        <van-button type="primary" size="large" @click="regist"></van-button>
        <div
          style="position: absolute;
        top: 300px;    font-size: 13px;color: #666;
        left: -65px;"
        ></div>
      </van-tab>

      <van-tab title="">
        <van-cell-group>
          <van-field
            v-model="username"
            clearable
            placeholder="Email /"
            @click-right-icon="$toast('question')"
          />
          <van-field
            v-model="password"
            type="password"
            right-icon="eye"
            placeholder="(6-20)"
          />
        </van-cell-group>
        <van-button type="primary" size="large" style="top:265px" @click="login"></van-button>
        <div
          style="position: absolute;
        top: 317px;    font-size: 13px;color: #666;
        right:-60px;"
        ></div>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>

export default {
  methods: {
    onClick() {
      // this.$toast(title);
    },
    link() {
      this.$router.go(-1);
    },
    async regist() {
      if (this.password.trim() != "" && this.username.trim() != "") {
        let status = await this.getData(
          "get",
          `//10.3.132.11:12345/regist?username=${this.username}`
        );
        if (status.data == 1) {
          let msg = await this.getData(
            "post",
            "//10.3.132.11:12345/regist",
            {
              username: this.username,
              password: this.password
            }
          );
          //   console.log('',msg.data)
          if (msg.data == 1) {
            alert("");
            this.username = "";
            this.password = "";
          }
        } else if (status.data == 0) {
          alert("");
        }
      } else {
        alert("");
      }
    },
    async login() {
      if (this.username.trim() != "" && this.password.trim() != "") {
        let status = await this.getData(
          "post",
          '//10.3.132.11:12345/login',
          {
              username: this.username,
              password: this.password
            }
        );
        //status.data,token,0
        // console.log(status.data)
        if(status.data===0){
            alert('')
        }else{
            //localStorage1
            localStorage.setItem('isLogin',1);
            //localStorage
            localStorage.setItem('username',`${this.username}`);
            //
            this.$router.push({name:'main'});
        }
      }
    }
  },
  data() {
    return {
      username: "",
      password: ""
    };
  }
};
</script>
<style scoped lang="scss" >
.login {
  width: auto;
  height: 38px;
  padding: 3px 12px;
  border-bottom: 1px solid #eeeeee;
  position: relative;
  .van-icon {
    float: left;
    height: 38px;
    line-height: 50px;
    margin-top: -1px;
  }
  .van-tabs {
    width: 60%;
    height: 44px;
    position: absolute;
    bottom: 0;
    left: 20%;
    .van-tab {
      width: 100px !important;
      height: 20px;
      padding: 12px;
      border: none;
    }
  }
  .van-hairline--top-bottom::after {
    border-top: none;
  }
  .van-cell-group {
    width: 345px;
    margin-top: 50px;
    // position: absolute ;
    left: -60px;
    .van-field {
      height: 47px;
      margin-top: 24px;
    }
  }
  .van-cell-group::after {
    border-top: none;
  }
  .van-button {
    width: 353px;
    height: 40px;
    background: #cccccc;
    position: absolute;
    border: none;
    top: 240px;
    left: -65px;
  }
}
</style>

