import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

import Home from './pages/Home.vue'

//
import Allgoods from './pages/Allgoods.vue'
//
import Newgoods from './pages/Newgoods.vue'
//
import Ncategory from './pages/Ncategory.vue'
import Login from './pages/Zlogin.vue'
import Zshezhi from './pages/Zshezhi.vue'
import Lrizhi from './pages/Lrizhi.vue'
import Lcollect from './pages/Lcollect.vue'
import Lclient from './pages/Lclient.vue'
import Lcoupon from './pages/Lcoupon.vue'
import Lmyorder from './pages/Lmyorder.vue'
import Search from './pages/Search'
import Lmessage from './pages/Lmessage'
import Lactivity from './pages/Lactivity'
import Zdetail from './pages/Zdetail'
const router = new Router({
  routes: [
    //
    { path: '/', redirect: { name: 'main' } },
    //
    //Home
    {
      path: '/home',
      name: 'home',
      component: Home,
      //()
      children: [
        //main 
        {
          path: 'sales',
          name: 'sales',
          component: Home.components.Lsales
        }, {
          path: 'women',
          name: 'women',
          component: Home.components.Zwomen
        }, {
          path: 'man',
          name: 'man',
          component: Home.components.Zman
        }, {
          path: 'kids',
          name: 'kids',
          component: Home.components.Zkids
        }, {
          path: 'baby',
          name: 'baby',
          component: Home.components.Zbaby
        }, {
          path: 'sports',
          name: 'sports',
          component: Home.components.Zsports
        },
        // footer
        //
        {
          path: 'main',
          name: 'main',
          component: Home.components.Nhome
        },
        //
        {
          path: 'fenlei/:aa',
          name: 'fenlei',
          component: Home.components.Zfenlei,
        },
        //
        {
          path: 'jingxuan',
          name: 'jingxuan',
          component: Home.components.Ngood,
        },
        //
        {
          path: 'gouwuche',
          name: 'gouwuche',
          component: Home.components.Zgouwuche,
          //login
          beforeEnter: function(to,from,next){
            let isLogin = localStorage.getItem('isLogin');
            if(isLogin){
              next()
            }else{
              router.push({
                name:'login'
              })
            }
          }
        },
        //
        {
          path: 'geren',
          name: 'geren',
          component: Home.components.Zgeren,
          //login
          beforeEnter: function(to,from,next){
            let isLogin = localStorage.getItem('isLogin');
            if(isLogin){
              next()
            }else{
              router.push({
                name:'login'
              })
            }
          }
        }
      ]
    },
    //
    {
      path:'/login',
      name:'login',
      component: Login
    },

    {
      path:'/allgoods',
      name:'allgoods',
      component:Allgoods
    },
    {
      path:'/newgoods',
      name:'newgoods',
      component:Newgoods
    },
    {
      path:'/category',
      name:'category',
      component:Ncategory
    },
    //
    {
      path:'/shezhi',
      name:'shezhi',
      component: Zshezhi
    },
    //
    {
      path: '/rizhi',
      name: 'rizhi',
      component: Lrizhi
    },
    {
      path: '/collect',
      name: 'collect',
      component: Lcollect
    },
    {
      path: '/client',
      name: 'client',
      component: Lclient
    },
    //
    {
      path:'/message',
      name:'message',
      component: Lmessage
    },
    //->
    {
      path:'/activity',
      name:'activity',
      component: Lactivity
    },
    //
    {
      path:'/detail/:id',
      name:'detail',
      component: Zdetail
    },
    //
    {
      path: '/coupon',
      name: 'coupon',
      component: Lcoupon
    },
    {
      path: '/myorder',
      name: 'myorder',
      component: Lmyorder
    },
    {
      path:'/search',
      name:'search',
      component:Search
    }
  ]
})

export default router
